.about-client {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
}

.about-client__column {
  width: 1106px;
  height: 558px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  grid-template-columns: 402px 1fr;
  grid-template-rows: 1fr 1fr;
  column-gap: 51px;
  row-gap: 24px;
}

.about-client__image_1 {
  grid-area: 1 / 1 / 2 / 2;
}

.about-client__image_2 {
  grid-area: 2 / 1 / 3 / 2;
}

.about-client__texts {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  grid-area: 1 / 2 / 3 / 3;
}

.about-client__texts-title {
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 43px;
  text-align: right;
  text-transform: uppercase;
  color: #FFFFFF;
}

.about-client__texts-subtitle {
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 29px;
  text-align: right;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 45px;
}

.about-client__texts-heading {
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  text-align: right;
  text-transform: uppercase;
  color: #B68F33;
  margin-bottom: 12px;
}

.about-client__texts-text {
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  text-align: right;
  color: #FFFFFF;
  margin-bottom: 25px;
}