.footer__title {
  width: 100%;
  margin-bottom: 22px;
}

.footer__texts {
  margin: 0 40px 38px 40px;
  display: flex;
  justify-content: space-between;
}

.footer__text {
  font-family: Audrey;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  text-transform: uppercase;
  color: rgba(182, 143, 51, 0.8);
}

.footer__copyright {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  margin: 0 15px 24px 0;
}