.main {
  position: relative;
}

@media screen and (max-width: 768px) {
  .main {
    height: 100vh;
    margin: 0 0 63px 0;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .main {
    height: 100vh;
    margin: 0 0 210px 0;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .main {
    height: 720px;
    margin: 0 0 300px 0;
  }
}

@media screen and (min-width: 1440px) {
  .main {
    height: 720px;
    margin: 0 0 300px 0;
  }
}


.main__title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  grid-template-columns: max-content 1fr;
  justify-items: center;
  align-items: center;
  z-index: 1;
}

@media screen and (max-width: 768px) {
  .main__title {
    column-gap: 11px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .main__title {
    column-gap: 36px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .main__title {
    column-gap: 27px;
  }
}

@media screen and (min-width: 1440px) {
  .main__title {
    column-gap: 27px;
  }
}

.main__title-text {
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  /*line-height: 119px;*/
  text-transform: uppercase;
  transition: 1s;
}

@media screen and (max-width: 768px) {
  .main__title-text {
    font-size: 36px;
    line-height: 36px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .main__title-text {
    font-size: 72px;
    line-height: 72px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .main__title-text {
    font-size: 98px;
    line-height: 98px;
  }
}

@media screen and (min-width: 1440px) {
  .main__title-text {
    font-size: 98px;
    line-height: 98px;
  }
}

.main__title-text_position {
  position: absolute;
  top: 0;
}

@media screen and (max-width: 768px) {
  .main__title-text_position {
    right: -25px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .main__title-text_position {
    right: -41px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .main__title-text_position {
    right: -93px;
  }
}

@media screen and (min-width: 1440px) {
  .main__title-text_position {
    right: -93px;
  }
}

.main__title-text_image {
  opacity: 0;

  display: grid;
  grid-template-columns: repeat(2, min-content);
  align-items: top;
}

@media screen and (max-width: 768px) {
  .main__title-text_image {
    column-gap: 6px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .main__title-text_image {
    column-gap: 10px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .main__title-text_image {
    column-gap: 13px;
  }
}

@media screen and (min-width: 1440px) {
  .main__title-text_image {
    column-gap: 13px;
  }
}

.main__title-text_color_gold {
  color: #B68F33;
}

