.main__title-line {
  justify-self: start;
  background-color: #B68F33;
}

@media screen and (max-width: 768px) {
  .main__title-line {
    width: 72px;
    height: 2px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .main__title-line {
    width: 148px;
    height: 4px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .main__title-line {
    width: 200px;
    height: 5px;
  }
}

@media screen and (min-width: 1440px) {
  .main__title-line {
    width: 200px;
    height: 5px;
  }
}

.main__title-overlay {
  grid-column-start: 1;
  grid-column-end: 3;
  justify-self: end;
  position: relative;
  width: 100%;
}

@media screen and (max-width: 768px) {
  .main__title-overlay {
    height: 36px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .main__title-overlay {
    height: 72px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .main__title-overlay {
    height: 98px;
  }
}

@media screen and (min-width: 1440px) {
  .main__title-overlay {
    height: 98px;
  }
}

.main__side-text {
  position: absolute;
  top: 336px;
  width: 720px;
  height: 47px;
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 54px;
  line-height: 47px;
  text-align: center;
  text-transform: uppercase;
  color: rgb(182, 143, 51);
  opacity: 0.2;
  transform: rotate(-90deg);
  z-index: -1;
}

@media screen and (max-width: 768px) {
  .main__side-text {
    display: none;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .main__side-text {
    left: -250px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .main__side-text {
    left: -151px;
  }
}

@media screen and (min-width: 1440px) {
  .main__side-text {
    left: -151px;
  }
}

.main__side-text_color {
  opacity: 0.5;
}

.main__upper-text {
  position: absolute;
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.2);
  z-index: -1;
}

@media screen and (max-width: 768px) {
  .main__upper-text {
    bottom: calc(20% + 100px);
    left: 0;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .main__upper-text {
    top: 68px;
    left: 50%;
    transform: translate(-50%, 0);
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .main__upper-text {
    top: 75px;
    left: 50%;
    transform: translate(-50%, 0);
  }
}

@media screen and (min-width: 1440px) {
  .main__upper-text {
    top: 75px;
    left: 50%;
    transform: translate(-50%, 0);
  }
}

