@keyframes logo-background {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main__logo-background {
  position: absolute;
  width: 735px;
  height: 735px;
  animation: logo-background 180s infinite;
  z-index: -1;
  opacity: 0.1;
}

@media screen and (max-width: 768px) {
  .main__logo-background {
    top: 8px;
    right: -120px;
  }
}

