.experience {
  margin: 0 38px 255px 38px;
}

.experience__title {
  align-self: start;
  justify-self: start;
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: left;
  text-transform: uppercase;
  color: #B68F33; 
  margin-bottom: 67px;
}

.experience__column {
  display: grid;
  grid-template-columns: 515px 1fr;
  /*grid-template-rows: 18px 1fr;*/
  column-gap: 78px;
  /*row-gap: 83px;*/
}

.experience__texts {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 3;
  display: grid;
  grid-template-columns: 291px 1fr;
  grid-template-rows: 1fr min-content;
  column-gap: 48px;
  row-gap: 16px;
}

