  column-gap: 51px;
  padding-top: 31px;
  border-top: 1px solid rgba(255, 255, 255, 0.4);
}*/

.experience__texts-main {
  align-self: end;
  justify-self: end;
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  color: rgba(255, 255, 255, 0.9);
  padding-bottom: 35px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  grid-area: 1 / 1 / 2 / 3;
}

.experience__texts-additional {
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.8);
}
