.more {
  width: 100%;
  height: 433px;
  position: relative;
  margin: 0 93px 400px 73px;
}

.more__title {
  position: absolute;
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 164px;
  line-height: 197px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
}

.more__title_number_1 {
  top: 0;
  left: 0;
}

.more__title_number_2 {
  top: 91px;
  left: 594px;
}

@keyframes more-title {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(100%, 0);
  }
  51% {
    transform: translate(-100%, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}

.more__title_number_3 {
  position: relative;
  color: #B68F33;
}

.more__title-overlay {
  position: absolute;
  top: 233px;
  left: 599px;
  overflow: hidden;
  transition: 1s;
}

.more__image {
  position: absolute;
  top: 141px;
  left: 361px;
  animation: logo-background 180s infinite;
  transition: 0.5s;
}

.more__image:hover {
  opacity: 0.8;
}

.more__line {
  position: absolute;
  top: 330px;
  left: 870px;
  width: 278px;
  height: 6px;
  background: rgba(255, 255, 255, 0.8);
  transform: rotate(-54deg);
  z-index: -1;
}