.searchbar {
  visibility: visible;
  position: fixed;
  left: 50%;
  transform: translate(-50%, 0);
  /*display: flex;
  justify-content: space-between;
  align-items: center;*/
  display: grid;
  grid-template-columns: 1fr max-content;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .searchbar {
    width: calc(100% - 24px);
    height: 20px;
    bottom: 15px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .searchbar {
    width: calc(100% - 72px);
    bottom: 13px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .searchbar {
    width: 1200px;
    height: 76px;
    bottom: 13px;
    padding: 30px 0 ;
  }
}

@media screen and (min-width: 1440px) {
  .searchbar {
    width: 1200px;
    height: 76px;
    bottom: 13px;
    padding: 30px 0;
  }
}

.searchbar__links {
  display: grid;
}

@media screen and (max-width: 768px) {
  .searchbar__links {
    position: absolute;
    grid-template-rows: repeat(4, max-content);
    row-gap: 13px;
    top: -120px;
    right: 0;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .searchbar__links {
    grid-template-columns: repeat(4, max-content);
