.clients {
  margin: 0 40px 556px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.clients__title {
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 38px;
  line-height: 46px;
  text-transform: uppercase;
  color: #B68F33;
  text-align: center;
  padding-bottom: 15px;
  border-bottom: 1px solid #FFFFFF;
  width: max-content;
  margin-bottom: 72px;
}

.clients__logos {
  width: 100%;
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-auto-rows: 78px;
  justify-items: center;
  align-items: center;
  /*display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;*/
  margin: 0 40px 80px 40px;
}

.clients__logos-image {
  object-fit: cover;
  transition: 0.5s;
  justify-self: start;
}

.clients__logos-image:nth-child(even) {
  justify-self: end;
}

/*.clients__logos-image:nth-child(n + 6) {
  justify-self: end;
}*/

.clients__logos-image:hover {
  opacity: 0.6;
  transform: scale(0.9, 0.9);
}

.clients__overlay {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.clients__overlay-line {
  width: 134px;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.6);
  margin-bottom: 19px;
}

.clients__subtitle {
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.6);
}

.clients__subtitle-link {
  text-decoration: none;
}