.menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  visibility: hidden;
}

.menu__column {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  visibility: hidden;
}

.menu__column_1 {
  display: grid;
  grid-template-columns: min-content 1fr;
  align-items: center;
  z-index: 3;
}

@media screen and (max-width: 768px) {
  .menu__column_1 {
    top: 17px;
    width: calc(100% - 24px);
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .menu__column_1 {
    top: 43px;
    width: calc(100% - 72px);
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .menu__column_1 {
    top: 50px;
    width: 1200px;
  }
}

@media screen and (min-width: 1440px) {
  .menu__column_1 {
    top: 50px;
    width: 1200px;
  }
}

.menu__column_2 {
  /*z-index: 1;*/
  height: 100%;
}


.menu__background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
}

.menu__links {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}

@media screen and (max-width: 768px) {
  .menu__links {
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    /*grid-template-rows: repeat(4, 18px);
