.menu__link {
  position: relative;
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  text-transform: uppercase;
  color: #B68F33;
  transition: 1s;
  padding: 2px;
  z-index: 1;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
